.boxCon {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.boxCon .box {
    padding: 2rem;
    border-radius: 2rem;
    border: 5px solid lightgray;
    text-align: center;
}
.boxCon .box img {
  width: 80%;
  height: 80%;
  object-fit: cover;

}
.btnCon {
  width: calc(50% - 1rem);
  padding: 2rem;
  text-align: center;
}
.box.lose {
  background-color: rgba(255, 0, 0, 0.5);
}
.box.win {
  background-color: rgba(0, 255, 0, 0.5);
}
.box.draw {
  background-color: rgba(255, 255, 0, 0.5);
}